import React, { useState, useEffect } from "react";
import { Box, LinearProgress } from "@mui/material";
import {
  DataGrid,
  GridColDef,
  GridRowSelectionModel,
  GridRowIdGetter,
} from "@mui/x-data-grid";
import useUrlParams from "../hooks/useUrlParams";

interface DataGridTableParams {
  onSelectionChange: (rowSelected: GridRowSelectionModel, data: any) => void;
  columns: GridColDef[];
  initialPage?: number;
  rowHeight?: number;
  queryKey: Array<string>;
  dataKey?: string;
  getRowId: GridRowIdGetter<any> | undefined;
  rows: GridRowSelectionModel;
  setRows: React.Dispatch<React.SetStateAction<GridRowSelectionModel>>;
  isLoading: boolean;
  mutipleSelect: boolean;
  height?: string;
}

export default function DataGridTable({
  onSelectionChange,
  initialPage = 10,
  columns,
  rowHeight = 35,
  getRowId,
  rows,
  isLoading,
  mutipleSelect,
  height = "500px",
}: DataGridTableParams) {
  const { searchParams, setSearchParams } = useUrlParams();
  const [rowSelectionModel, setRowSelectionModel] =
    useState<GridRowSelectionModel>([]);
  const [doubleClick, setDoubleClick] = useState(false);
  const [paginationModel, setPaginationModel] = React.useState({
    pageSize: parseInt(searchParams.get("pageSize") as string) || 100,
    page: parseInt(searchParams.get("page") as string) || 1,
  });

  useEffect(() => {
    setRowSelectionModel(
      (searchParams.get("selected") as string) === ""
        ? []
        : [searchParams.get("selected") as string]
    );
  }, [searchParams]);

  useEffect(() => {
    onSelectionChange(rowSelectionModel, rows);
  }, [doubleClick]);

  if (isLoading) {
    const updateColumns: any = columns.map((column) => {
      if (column.field === "id") {
        column = { ...column, colSpan: 4, align: "center" };
      }
      return column;
    });
    return (
      <Box
        sx={{
          height: 500,
          width: "100%",
          overflowX: "scroll",
          position: "absolute",
        }}
      >
        <DataGrid
          slots={{ loadingOverlay: LinearProgress }}
          loading={isLoading}
          rows={[{ id: "Loading..." }]}
          columns={updateColumns}
        />
      </Box>
    );
  }
  return (
    <Box
      sx={{
        height: height,
        width: "100%",
        overflowX: "scroll",
        position: "absolute",
      }}
    >
      <button
        id="reset-data-grid-selection"
        style={{ display: "none" }}
        onClick={() => {
          setRowSelectionModel([]);
        }}
      >
        reset
      </button>
      {mutipleSelect ? (
        <DataGrid
          checkboxSelection
          disableRowSelectionOnClick
          slots={{ loadingOverlay: LinearProgress }}
          loading={isLoading}
          rowHeight={rowHeight}
          rows={rows}
          columns={columns}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: initialPage },
            },
          }}
          pageSizeOptions={[initialPage, 25, 50, 100]}
          getRowId={getRowId}
        />
      ) : (
        <DataGrid
          slots={{ loadingOverlay: LinearProgress }}
          loading={isLoading}
          rowHeight={rowHeight}
          rows={rows}
          columns={columns}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: initialPage },
            },
          }}
          checkboxSelection
          pageSizeOptions={[initialPage, 25, 50, 100]}
          onRowDoubleClick={(params) => {
            setDoubleClick((d) => !d);
            setRowSelectionModel((data) => {
              if (data.length > 0 && data[0] === params.id) {
                return [];
              }
              return [params.id];
            });
          }}
          onRowSelectionModelChange={(selection) => {
            if (selection.length > 1) {
              const selectionSet = new Set(rowSelectionModel);
              const result = selection.filter((s) => !selectionSet.has(s));
              onSelectionChange(result, rows);
              setRowSelectionModel(result);
            } else {
              onSelectionChange(selection, rows);
              setRowSelectionModel(selection);
            }
          }}
          disableRowSelectionOnClick
          rowSelectionModel={rowSelectionModel}
          sx={{
            "& .MuiDataGrid-columnHeaderCheckbox .MuiDataGrid-columnHeaderTitleContainer":
              {
                display: "none",
              },
            "& .MuiDataGrid-row": {
              cursor: "pointer",
            },
          }}
          getRowId={getRowId}
          paginationModel={paginationModel}
          onPaginationModelChange={(model) => {
            setPaginationModel(model);
            setSearchParams((prev) => {
              prev.set("page", model.page.toString());
              prev.set("pageSize", model.pageSize.toString());
              return prev;
            });
          }}
        />
      )}
    </Box>
  );
}
