import { createContext, useContext, useReducer, useState } from "react";
import BreadCrumbs from "../../component/BreadCrumbs";
import { Outlet } from "react-router-dom";
import { Box, Button, CircularProgress } from "@mui/material";
import { green } from "@mui/material/colors";
import { pink } from "@mui/material/colors";
import AddIcon from "@mui/icons-material/Add";
import { AuthContext } from "../../../../../../../components/AuthContext";
import { useQuery, useMutation, useQueryClient } from "react-query";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import Swal from "sweetalert2";
import DeleteIcon from "@mui/icons-material/Delete";
import { GridRowSelectionModel } from "@mui/x-data-grid";
import { FormControl, InputLabel, Select, MenuItem } from "@mui/material";
import ContentPasteSearchIcon from "@mui/icons-material/ContentPasteSearch";
import ModalWithTable from "../../../../../../../components/ModalWithTable";
import storedLocalStorage from "../../../../../../../lib/localStorage";

const FirePolicyComponents = [
  {
    link: "/dashboard/task/production/policy/fire",
    text: "Policy Information",
  },
  {
    link: "/dashboard/task/production/policy/fire/policy-premium",
    text: "Policy Premium",
  },
];

const jsongStringInitialState = {
  form_action: "REG",
  form_type: "COM",
  sub_account: "HO",
  //insurer info
  client_id: "",
  client_name: "",
  client_address: "",
  //agent info
  agent_id: "",
  agent_name: "",
  agent_com: "0.00",
  //Vehicle policy
  PolicyAccount: "",
  PolicyNo: "",
  bill_no: "",
  //Period Insurance
  DateFrom: new Date(),
  DateTo: new Date(),
  DateIssued: new Date(),
  //Insured Unit
  locRisk: "charles1",
  propertyInsured: "",
  construction: "",
  occupancy: "",
  boundaries: "",
  //policy premuim
  mortgage: "",
  warranties: "",
  insuredValue: "0.00",
  percentagePremium: "0.00",
  totalPremium: "0.00",
  vat: "0.00",
  docStamp: "0.00",
  localGovTaxPercent: "0.75",
  localGovTax: "0.00",
  fsTax: "0.00",
  totalDue: "0.00",
  // extra
  fireActioMode: "",
};

const storagePolicy_ = "FPolicyInitialState";
const reducer = (state: any, action: any) => {
  switch (action.type) {
    case "UPDATE_FIELD":
      const newState = {
        ...state,
        [action.field]: action.value,
      };
      localStorage.setItem(storagePolicy_, JSON.stringify(newState));
      return newState;
    default:
      return state;
  }
};
export const FireContext = createContext<any>({});

const initialState = storedLocalStorage(
  jsongStringInitialState,
  storagePolicy_
);
const queryKeySearchPolicy = "fire-search";
const queryKeySearchClientEntry = "clients";
const queryKeySearchAgentEntry = "agents";
const queryKeyNeedData = "fire-policy";
const queryKeyAddOrUpdatePolicy = "fire-policy";
const queryKeyDeletePolicy = "fire-policy";

export const vpolicyColumn = [
  { field: "DateIssued", headerName: "Date", width: 200 },
  { field: "PolicyNo", headerName: "Policy No", width: 250 },
  {
    field: "Account",
    headerName: "Account",
    width: 170,
  },
  {
    field: "client_fullname",
    headerName: "Full Name",
    flex: 1,
  },
];

const vpolicyKey = "vehicle-policy";

export default function FirePolicy() {
  const [state, dispatch] = useReducer(reducer, initialState);
  const { myAxios, user } = useContext(AuthContext);
  const [clientRows, setClientRows] = useState<GridRowSelectionModel>([]);
  const [agentRows, setAgentRows] = useState<GridRowSelectionModel>([]);
  const [search, setSearch] = useState<GridRowSelectionModel>([]);
  const [searchShow, setSearchShow] = useState(false);
  const [loadSearchingChange, setLoadSearchingChange] = useState(false);
  const [Mortgagee, setMortgagee] = useState(false);
  const [showField, setShowField] = useState({
    thirdparty: state.form_type.toLowerCase() === "tpl",
    compre: state.form_type.toLowerCase() === "com",
  });
  const queryClient = useQueryClient();
  const isAddOrEditMode = state.fireActioMode === "";

  const { isLoading: searchLoading } = useQuery({
    queryKey: queryKeySearchPolicy,
    queryFn: async () =>
      await myAxios.get(
        `/task/production/search-fire-policy?searchFirePolicy=`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      const response = res.data as any;
      setSearch(response.firePolicy);
    },
  });

  const { isLoading: clientLoading } = useQuery({
    queryKey: queryKeySearchClientEntry,
    queryFn: async () =>
      await myAxios.get(
        `/task/production/search-client-fire-policy?searchClient=`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      const response = res.data as any;
      setClientRows(response.clients);
    },
  });

  const { isLoading: agentLoading } = useQuery({
    queryKey: queryKeySearchAgentEntry,
    queryFn: async () =>
      await myAxios.get(
        `/task/production/search-agent-fire-policy?searchAgent=`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      const response = res.data as any;
      setAgentRows(response.agents);
    },
  });

  const { data, isLoading } = useQuery({
    queryKey: queryKeyNeedData,
    queryFn: async () =>
      await myAxios.get(`/task/production/get-fire-policy`, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      }),
  });

  const { mutate, isLoading: loadingAddNew } = useMutation({
    mutationKey: queryKeyAddOrUpdatePolicy,
    mutationFn: async (variables: any) => {
      if (state.fireActioMode === "delete") {
        return await myAxios.post(
          "/task/production/update-fire-policy",
          variables,
          {
            headers: {
              Authorization: `Bearer ${user?.accessToken}`,
            },
          }
        );
      }
      return await myAxios.post("/task/production/add-fire-policy", variables, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      });
    },
    onSuccess: async (res) => {
      console.log(res);
      if (res.data.success) {
        await updateQueryByKey();
        return Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          backToDefaultState(jsongStringInitialState, true);
        });
      }

      Swal.fire({
        position: "center",
        icon: "error",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });
  const { mutate: mutateDelete, isLoading: loadingDelete } = useMutation({
    mutationKey: queryKeyDeletePolicy,
    mutationFn: async (variables: any) => {
      return await myAxios.post(
        "/task/production/delete-fire-policy",
        variables,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      );
    },
    onSuccess: async (res) => {
      if (res.data.success) {
        await updateQueryByKey();
        return Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          backToDefaultState(jsongStringInitialState, true);
        });
      }

      Swal.fire({
        position: "center",
        icon: "error",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  const customInputchange = (value: any, name: string) => {
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  function handleOnSave() {
    if (state.client_id === "" || state.client_id === null) {
      return Swal.fire(
        "Unable to save! Invalid IDNo.",
        "you missed the Client Id Field?",
        "error"
      );
    }
    if (state.PolicyAccount === "" || state.PolicyAccount === null) {
      return Swal.fire(
        "Unable to save! Please select Account.",
        "you missed the Account Field?",
        "error"
      );
    }
    if (state.PolicyNo === "" || state.PolicyNo === null) {
      return Swal.fire(
        "Unable to save! Invalid Policy No.",
        "you missed the Policy No Field?",
        "error"
      );
    }
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Save it!",
    }).then((result) => {
      if (result.isConfirmed) {
        console.log(state);
        mutate(state);
      }
    });
  }
  function computation() {
    const inpInsurerVal = parseFloat(state.insuredValue);
    const inPercentagePremium = parseFloat(state.percentagePremium);
    const inpLocalGovTaxPercent = parseFloat(state.localGovTaxPercent);

    const Percentage = inPercentagePremium / 100;
    const VatPercentage = 12 / 100;
    const DocPercentage = 12.5 / 100;
    const LOGPercentage = inpLocalGovTaxPercent / 100;
    const FsTaxPercentage = 2 / 100;
    const NewTotalPremium = inpInsurerVal * Percentage;

    customInputchange(inpInsurerVal.toFixed(2), "insuredValue");
    customInputchange(inPercentagePremium.toFixed(2), "percentagePremium");
    customInputchange(NewTotalPremium.toFixed(2), "totalPremium");
    customInputchange((VatPercentage * NewTotalPremium).toFixed(2), "vat");
    customInputchange((DocPercentage * NewTotalPremium).toFixed(2), "docStamp");
    customInputchange(
      (LOGPercentage * NewTotalPremium).toFixed(2),
      "localGovTax"
    );
    customInputchange((FsTaxPercentage * NewTotalPremium).toFixed(2), "fsTax");
    customInputchange(
      (
        parseFloat(NewTotalPremium.toFixed(2)) +
        parseFloat((VatPercentage * NewTotalPremium).toFixed(2)) +
        parseFloat((DocPercentage * NewTotalPremium).toFixed(2)) +
        parseFloat((LOGPercentage * NewTotalPremium).toFixed(2)) +
        parseFloat((FsTaxPercentage * NewTotalPremium).toFixed(2))
      ).toFixed(2),
      "totalDue"
    );
  }

  function backToDefaultState(json: any, resetAll: boolean = false) {
    json.form_type = state.form_type;
    json.form_action = state.form_action;
    if (!resetAll) {
      json.fireActioMode = state.fireActioMode;
    }
    Object.entries(json).forEach(([key, value]) => {
      customInputchange(value, key);
    });
  }

  async function updateQueryByKey() {
    return Promise.all([
      queryClient.invalidateQueries(queryKeySearchPolicy),
      queryClient.invalidateQueries(queryKeySearchClientEntry),
      queryClient.invalidateQueries(queryKeySearchAgentEntry),
      queryClient.invalidateQueries(queryKeyNeedData),
      queryClient.invalidateQueries(queryKeyAddOrUpdatePolicy),
      queryClient.invalidateQueries(queryKeyDeletePolicy),
    ]);
  }

  if (isLoading) {
    return <h1>Loading....</h1>;
  }
  return (
    <FireContext.Provider
      value={{
        clientLoading,
        agentLoading,
        state,
        handleInputChange,
        customInputchange,
        Mortgagee,
        setMortgagee,
        showField,
        setShowField,
        isLoading,
        clientRows,
        setClientRows,
        myAxios,
        user,
        agentRows,
        setAgentRows,
        data,
        computation,
        isAddOrEditMode,
      }}
    >
      <BreadCrumbs list={FirePolicyComponents} />
      <Box
        sx={(theme) => ({
          display: "flex",
          alignItems: "center",
          columnGap: "20px",
          marginBottom: "10px",
          [theme.breakpoints.down("sm")]: {
            flexDirection: "column",
            alignItems: "flex-start",
            flex: 1,
          },
        })}
      >
        <div
          style={{
            marginTop: "10px",
            marginBottom: "12px",
            flex: 1,
            width: "100%",
          }}
        ></div>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            columnGap: "20px",
          }}
        >
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "5px",
            }}
          >
            {state.fireActioMode === "" && (
              <Button
                variant="contained"
                startIcon={<AddIcon />}
                onClick={() => {
                  customInputchange("add", "fireActioMode");
                }}
              >
                New
              </Button>
            )}

            <Box sx={{ position: "relative" }}>
              <Button
                color="primary"
                variant="contained"
                type="submit"
                onClick={handleOnSave}
                disabled={loadingAddNew || state.fireActioMode === ""}
                startIcon={<SaveIcon />}
              >
                Save
              </Button>

              {loadingAddNew && state.fireActioMode === 'add' && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: green[500],
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    marginTop: "-12px",
                    marginLeft: "-12px",
                  }}
                />
              )}
            </Box>
            {state.fireActioMode !== "" && (
              <Button
                variant="contained"
                startIcon={<CloseIcon />}
                color="error"
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, cancel it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      customInputchange("", "fireActioMode");
                      backToDefaultState(jsongStringInitialState, true);
                    }
                  });
                }}
              >
                Cancel
              </Button>
            )}
            <Box sx={{ position: "relative" }}>
              <Button
                id="save-entry-header"
                variant="contained"
                sx={{
                  backgroundColor: pink[500],
                  "&:hover": {
                    backgroundColor: pink[600],
                  },
                }}
                disabled={state.fireActioMode !== "delete"}
                startIcon={<DeleteIcon />}
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, delete it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      mutateDelete({
                        PolicyAccount: state.PolicyAccount,
                        PolicyNo: state.PolicyNo,
                      });
                    }
                  });
                }}
              >
                Delete
              </Button>

              {loadingDelete && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: green[500],
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    marginTop: "-12px",
                    marginLeft: "-12px",
                  }}
                />
              )}
            </Box>
          </div>
        </div>
      </Box>
      <div style={{ marginBottom: "5px", display: "flex", gap: "10px" }}>
        <Button
          size="small"
          variant="outlined"
          startIcon={<ContentPasteSearchIcon />}
          onClick={() => {
            if (state.fireActioMode === "" || state.fireActioMode === null) {
              return setSearchShow(true);
            }
            Swal.fire({
              title: "Are you sure?",
              text: "Your in Add mode, If you proceed the data you input will replace existing data. Are you sure you want to continue",
              icon: "warning",
              showCancelButton: true,
              confirmButtonColor: "#3085d6",
              cancelButtonColor: "#d33",
              confirmButtonText: "Yes",
              cancelButtonText: "No",
            }).then((result) => {
              if (result.isConfirmed) {
                customInputchange("", "fireActioMode");

                backToDefaultState(jsongStringInitialState, true);
              }
            });
          }}
          disabled={searchLoading || loadSearchingChange}
        >
          Search Policy
        </Button>
        <FormControl
          size="small"
          sx={(theme) => ({
            width: "150px",
          })}
        >
          <InputLabel id="subAccount">Sub Account</InputLabel>
          <Select
            size="small"
            labelId="subAccount"
            label="subAccount"
            name="sub_account"
            value={state.sub_account}
            onChange={(e) => {
              handleInputChange(e);
            }}
          >
            {(data?.data?.firePolicy.sub_account).map(
              (items: any, idx: number) => {
                return (
                  <MenuItem key={idx} value={items.Acronym.trim()}>
                    {items.Acronym}
                  </MenuItem>
                );
              }
            )}
          </Select>
        </FormControl>
      </div>
      <Outlet />

      <ModalWithTable
        showModal={searchShow}
        onCloseModal={() => {
          setSearchShow(false);
        }}
        onClickCloseIcon={() => {
          setSearchShow(false);
        }}
        searchOnChange={(e) => {
          setLoadSearchingChange(true);
          myAxios
            .get(
              `/task/production/search-fire-policy?searchFirePolicy=${e.target.value}`,
              {
                headers: {
                  Authorization: `Bearer ${user?.accessToken}`,
                },
              }
            )
            .then((res: any) => {
              if (!res?.data.success) {
                return alert(`Error : ${res?.data.message}`);
              }
              setSearch(res?.data.firePolicy);
              setLoadSearchingChange(false);
            });
        }}
        height={300}
        isLoading={searchLoading || loadSearchingChange}
        queryKey={vpolicyKey}
        columns={vpolicyColumn}
        onSelectionChange={(rowSelectionModel, data) => {
          if (rowSelectionModel.length <= 0) {
            return;
          }
          const selectedIDs = new Set(rowSelectionModel);
          const selectedRowData = data.filter((row: any) => {
            return selectedIDs.has(row.PolicyNo.toString());
          });

          if (selectedRowData.length <= 0) return;

          const {
            PolicyNo,
            Account,
            DateFrom,
            DateTo,
            BillNo,
            Percentage,
            Location,
            InsuredValue,
            PropertyInsured,
            Constraction,
            Occupancy,
            Boundaries,
            Mortgage,
            Warranties,
            IDNo,
            SubAcct,
            DateIssued,
            AgentID,
            AgentCom,
            client_fullname,
            address,
            agent_fullname,
          } = selectedRowData[0];

          customInputchange(SubAcct, "sub_account");
          customInputchange(IDNo, "client_id");
          customInputchange(client_fullname, "client_name");
          customInputchange(address, "client_address");

          customInputchange(AgentID, "agent_id");
          customInputchange(agent_fullname, "agent_name");
          customInputchange(AgentCom, "agent_com");

          customInputchange(Account, "PolicyAccount");
          customInputchange(PolicyNo, "PolicyNo");
          customInputchange(BillNo, "bill_no");

          customInputchange(DateFrom, "DateFrom");
          customInputchange(DateTo, "DateTo");
          customInputchange(DateIssued, "DateIssued");

          customInputchange(Location, "locRisk");
          customInputchange(PropertyInsured, "propertyInsured");
          customInputchange(Constraction, "construction");
          customInputchange(Occupancy, "occupancy");
          customInputchange(Boundaries, "boundaries");
          customInputchange(Mortgage, "mortgage");
          customInputchange(Warranties, "warranties");

          state.insuredValue = InsuredValue;
          state.percentagePremium = Percentage;
          computation();

          customInputchange("delete", "fireActioMode");
          setSearchShow(false);
        }}
        id="PolicyNo"
        rows={search}
        setRows={setSearch}
      />
    </FireContext.Provider>
  );
}
