import React from "react";
import DataGridTable from "./DataGridTable";
import { GridRowSelectionModel } from "@mui/x-data-grid";
import { Modal, Box, Typography,IconButton ,TextField } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
interface ModalWithTableParams {
  height: number;
  isLoading: boolean;
  queryKey: string;
  columns: Array<any>;
  onSelectionChange: (rowSelectionModel: Array<any>, data: Array<any>) => void;
  setRows: React.Dispatch<React.SetStateAction<GridRowSelectionModel>>;
  rows: GridRowSelectionModel;
  id: string;
  onCloseModal: () => void;
  onClickCloseIcon: () => void;
  showModal:boolean,
  searchOnChange:React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement> | undefined
}

const style = {
    position: "absolute" as "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "70%",
    bgcolor: "background.paper",
    boxShadow: 24,
    p: 4,
    heigth: "auto",
  };
  
export default function ModalWithTable({
  height,
  isLoading,
  queryKey,
  columns,
  onSelectionChange,
  setRows,
  rows,
  id,
  onCloseModal,
  showModal,
  onClickCloseIcon,
  searchOnChange
}: ModalWithTableParams) {
  return (
    <Modal
      open={showModal}
      onClose={onCloseModal}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={style}>
        <div style={{ position: "absolute", top: "10px", right: "10px" }}>
          <IconButton
            aria-label="search-client"
            color="secondary"
            onClick={onClickCloseIcon}
          >
            <CloseIcon />
          </IconButton>
        </div>
        <Typography id="modal-modal-title" variant="h6" component="h2" mb={2}>
          Search Clients
        </Typography>
        <TextField
          size="small"
          label="Search"
          sx={{ marginBottom: "10px" }}
          fullWidth
          onChange={searchOnChange}
        />

        <div style={{ position: "relative", height: `${height + 20}px` }}>
          <DataGridTable
            height={`${height}px`}
            mutipleSelect={false}
            isLoading={isLoading}
            queryKey={[queryKey]}
            columns={columns}
            onSelectionChange={onSelectionChange}
            getRowId={(row) => row[`${id}`].toString()}
            rows={rows}
            setRows={setRows}
          />
        </div>
      </Box>
    </Modal>
  );
}
